import React from 'react';
import './flip_card.css';
// only works inside of values-we-follow in the About page at the mo.
export default class FlipCard extends React.Component {
  render () {
    return (
      <div className="cardBox">
    <div className="card">
      {this.props.children}
    </div>
  </div>
    )
  }
}

