import {
  HashRouter,
  Switch,
  Route,
  NavLink
} from "react-router-dom";
import './main.css';
import './page.css';
import React from 'react';

import About from "./pages/About";
import Contact from "./pages/Contact";
import Home from "./pages/Home";
import Team from "./pages/Team";
import Projects from "./pages/Projects";

import Socials from './components/Socials';
const lang = window.localStorage["language"] || "en";



class Language extends React.Component {

  switchLanguage(){
    window.localStorage["language"] = window.localStorage.language === "en"? "nl" : "en";
    window.location.reload();
  }

  render () {
    return (
      <div className="language">
        <button onClick={this.switchLanguage}>{window.localStorage.language === "en"? "Nederlands" : "English"}</button>
      </div>
    )
  }
}

class Header extends React.Component {

  toggleNav () {
    this.setState({navToggled:!this.state.navToggled})
  }

  hideNav() {
    this.setState({navToggled:false});
    window.scrollTo(0,0);
  }

  updateScroll () {
    this.setState({isSticky: window.scrollY > this.state.height})
  }

  constructor(props) {
    super(props);
    this.state = {
      navToggled:false
    }
    this.toggleNav = this.toggleNav.bind(this);
    this.hideNav = this.hideNav.bind(this);
    this.updateScroll = this.updateScroll.bind(this);
  }

  componentDidMount () {
    let headerEl = document.querySelector(".App header");
    let height = headerEl.clientHeight;
    this.setState({height: this.state.height || height});
    // headerEl.style.height = height+"px";
    window.addEventListener("scroll",this.updateScroll,false);
  }

  componentWillUnmount () {
    window.removeEventListener("scroll",this.updateScroll,false);
  }
  
  render () {
    return (
    // <header className = {this.state.isSticky ? 'isSticky' : ''}>
    <header>
      <img src="./logo.png" id='logo' alt="Parents Across Borders"/>
      <div id="nav-toggler" onClick={this.toggleNav} className={this.state.navToggled? 'toggled' : ''}>
          <div></div>
          <div></div>
          <div></div>
        </div>
      <Nav hideNav={this.hideNav} navToggled={this.state.navToggled}></Nav>
    </header>
    )
  }
}

let translatedNav = {
  home:{
    en:"Home",
    nl:"Home"
  },
  about: {
    en:"About",
    nl:"Over Ons"
  },
  projects: {
    en:"Our Projects",
    nl:"Onze Projecten"
  },
  team: {
    en:"Our Team",
    nl:"Onze Team"
  },
  contact: {
    en:"Contact",
    nl:"Contact"
  }
}

class Nav extends React.Component{
  
  render () {
    return (
      <nav onClick={this.props.hideNav} className={this.props.navToggled? 'toggled' : ''}>
          <NavLink exact activeClassName="nav-active" to="/">{translatedNav.home[lang]}</NavLink>
          <NavLink exact activeClassName="nav-active" to="/about">{translatedNav.about[lang]}</NavLink>
          <NavLink exact activeClassName="nav-active" to="/projects">{translatedNav.projects[lang]}</NavLink>
          <NavLink exact activeClassName="nav-active" to="/team">{translatedNav.team[lang]}</NavLink>
          <NavLink exact activeClassName="nav-active" to="/contact">{translatedNav.contact[lang]}</NavLink>
      </nav>
    )
  }
}

class Footer extends React.Component {
  render () {
    return (
      <footer>
        <Nav/>
        <Socials/>
        <Language/>
      </footer>
    )
  }
}


class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      navToggled: false,
      headerHeight:0
    }
    
  }

  

  render() {
    return (
    <HashRouter>
      <div className="App">
        <Header />
        <Switch>
          <Route exact path='/' component={Home}/>
          <Route path='/about' component={About}/>
          <Route path='/contact' component={Contact}/>
          <Route path='/team' component={Team}/>
          <Route path='/projects' component={Projects}/>
        </Switch>
        <Footer />
      </div>
    </HashRouter>
    )
  }
}


export default App;
