import React from 'react';
// import Title from '../components/Title';
import AltTitle from '../components/AltTitle';
import TextBlock from "./TextBlocks/Team.json";
import ImgText from '../components/ImgText';
import './css/team.css';
const lang = window.localStorage["language"];
class BoardMembers extends React.Component {
  constructor(props) {
    super(props);
    this.getElementList = this.getElementList.bind(this);
  }
  render() {
    return (
      <ul id="board">
      {
        this.getElementList()
      }
</ul>
    )
  }

  getElementList() {
    let kind = this.props.kind;
    return TextBlock[kind].map((member,index)=>{
      return (<li key={index}>
        <ImgText img={member.img}>
          <h3>
            {member.name}
          </h3>
          {(member[lang]? member[lang] : member["en"])}
        </ImgText>
      </li>)
    })
  }
}

export default class Team extends React.Component {
  render () {
    return (
      <main>
        {/* <Title background="./imgs/titles/home.png">{lang === "nl"? "Onze Team" : "Our Team"}</Title> */}
        <AltTitle>{lang === "nl"? "Onze Team" : "Our Team"}</AltTitle>
        <h2>Team</h2>
        <BoardMembers kind="Team"/>
        <h2>Board</h2>
        <BoardMembers kind="BoardMembers"/>
      </main>
    )
  }
}