import React from 'react';
import {Facebook, MapPin, Mail, Phone} from 'react-feather';

let socials = {
  phone: {
    link:"tel:+31612345678",
    icon: <Phone />,
    text:"+31612345678"
  },
  mail: {
    link:"mailto:contact@parentingacrossborders.org",
    icon: <Mail />,
    text:"contact@parentingacrossborders.org"
  },
  fb: {
    link:"https://www.facebook.com/Parenting-across-Borders-104148937808810",
    icon: <Facebook />,
    text:"Parenting Across Borders - Facebook"
  },
  location: {
    link:"https://maps.google.com/",
    icon: <MapPin />,
    text:"123 Building, City, Province, Netherlands"
  }
}



export default class Socials extends React.Component {
  constructor(props) {
    super(props);
    this.getElementArray = this.getElementArray.bind(this);
  }
  render () {
    return (
      <div className="socials">
        {
          this.getElementArray()
        }
        </div>
    )
  }

  getElementArray() {
    let socialJsxArray = [];

    for(let key in socials) {
      socialJsxArray.push(
        <a key={key} href={socials[key].link} target="_blank" rel="noreferrer">
          {socials[key].icon} {this.props.includeText? <span>{socials[key].text}</span> : null}
        </a>
      )
    }
    return socialJsxArray
  }
}