import React from 'react';
import Carousel, { autoplayPlugin } from '@brainhubeu/react-carousel';
import '@brainhubeu/react-carousel/lib/style.css';

export default class Title extends React.Component {
  
  render () {
    return (
    <div className="title-wrapper" style={{backgroundImage: `url(${this.props.background})`}}>
      <div className="title">{this.props.children}</div>
    </div>
    )
  }
}

export class TitleCarousel extends React.Component {
  constructor (props) {
    super (props);
    this.state = {
      images: this.props.images.map(e=>{
        return <img src={e} alt=""/>
      })
    }
  }
  render () {
    return (
    <div className="title-wrapper">
      <Carousel
    plugins={[
     'infinite',
    {
      resolve: autoplayPlugin,
      options: {
        interval: 2000,
      }
    },
  ]}   
  animationSpeed={1000}
  slides={this.state.images}
>
</Carousel>
    </div>
    )
  }
}