import React from 'react';
// import Title from '../components/Title';
import AltTitle from '../components/AltTitle';
import TextBlock from '../pages/TextBlocks/Projects.json';
import './css/projects.css';
const lang = window.localStorage["language"] || "en";

export default class Projects extends React.Component {
  render () {
    return (
      <main>
      {/* <Title background="./imgs/titles/home.png">{lang === "nl"? "Onze Projecten" : "Our Projects"}</Title> */}
      <AltTitle>{lang === "nl"? "Onze Projecten" : "Our Projects"}</AltTitle>
      <div className="content">
      {
          TextBlock.blocks.map((block,index)=>{
            let b = block[window.localStorage["language"]];
            return (<div key={index}>
              <h2>{b.title}</h2>
              <hr></hr>
              <p dangerouslySetInnerHTML={{ __html: b.content }}></p>
            </div>)
          })
        }
        <div id="flyer-wrapper">

        <img alt="flyer" id="flyer" src="./imgs/our-projects/flyer.png"></img>

        </div>
      </div>
      </main>
    )
  }
}