import React from 'react';
// import Title from '../components/Title';
import AltTitle from '../components/AltTitle';
import FlipCard from '../components/FlipCard';
import TextBlock from './TextBlocks/About.json';
import './css/about.css';
const lang = window.localStorage["language"] || "en";

class ValuesWeFollow extends React.Component {
  render () {
    return (
      <ul id="values-we-follow">
      {TextBlock.values.map((block,index)=>{
        let b = block.content[lang];
        return (
          <li key={index}>
          <FlipCard>
            <div className="front">{b.front}</div>
            <div className="back" style={{backgroundColor:block.color}}>{b.back}</div>
         </FlipCard>
          </li>
        )
      })}
    </ul>
    )
  }
}

export default class About extends React.Component {
  render () {
    return (
      <main>
        {/* <Title background='./imgs/titles/home.png'>{lang === "nl"? "Over Ons" : "About Us"}</Title> */}
        <AltTitle>{lang === "nl"? "Over Ons" : "About Us"}</AltTitle>
        <div className='content'>
        {
          TextBlock.blocks.map((block,index)=>{
            let b = block[window.localStorage["language"]];
            return (<div key={index}>
              <h2>{b.title}</h2>
              <hr></hr>
              <p dangerouslySetInnerHTML={{ __html: b.content }}></p>
            </div>)
          })
        }

          <h2>Values we follow</h2>
          <hr></hr>
          <ValuesWeFollow />
        </div>
      </main>
    )
  }
}