import React from 'react';
import './AltTitle.css';
export default class AltTitle extends React.Component {
  render () {
    return (
      <div className="AltTitleOuter">
        <div className="AltTitleInner">
          <div className="yellow-box"></div>
          <div className="content">{this.props.children}</div>
        </div>
      </div>
    )
  }
}